import React, { useState, useEffect } from 'react'
import Filter from './components/Filter'
import Header from './components/Header'
import Footer from './components/Footer'
import Notification from './components/Notification'
import personService from './services/persons'

const Form = (props) => {
  return (
    <div>
      <form onSubmit={props.addPerson}>
        <input placeholder="add name" value={props.newName} onChange={props.handleNameChange}/>
        <br/>
        <input placeholder="add number"value={props.newNumber} onChange={props.handleNumberChange}/>
        <br/>
        <button type="submit" >save</button>
      </form>
    </div>
  )
}

const Person = ({person, setPersons,setNotification,personObject,persons}) => {
  const removePerson = (event) => {
      event.preventDefault()
      if (window.confirm('do you want to delete?')) {
          console.log("yes")
          personService
          .remove(`${person.id}`)
          .then(setPersons(persons.filter(p => p.name !== person.name)))
          .catch(error => {
            setNotification(`${person.name} was already removed from the phonebook`)
          setTimeout(() => {
            setNotification(null)
          }, 2000)
          })
          setNotification(`${person.name} was successfully removed in the phonebook`)
          setTimeout(() => {
            setNotification(null)
          }, 2000)
      } else {
          console.log("no")
      }
    }
  return (
      <div>
          <li> {person.name} {person.number} 
          <button onClick={removePerson}>  delete </button>
          </li>
      </div>
  )
}

const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ filter, setFilter] = useState('')
  const [notification, setNotification] = useState(null)


  useEffect(() => {
    console.log('effect')
    personService
      .getAll()
      .then(initialPersons => {
        console.log('got all')
        setPersons(initialPersons)
      })
  }, [])

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }
  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }
  const handleFilter = (event) => {
    setFilter(event.target.value)
  }
  const addPerson = (event) => {
    event.preventDefault()
    const personObject = {
      name: newName,
      number: newNumber
    }

    const nameList = persons.map(p => p.name)
    const numberList = persons.map(p => p.number)
    if(nameList.includes(personObject.name)) {
      window.alert(`${personObject.name} already in phonebook`)
      const confirm = window.confirm(`would you like to change the number for ${personObject.name}?`)
      if(confirm) {
        const toUpdateID = persons.find(p => p.name === personObject.name).id
        personService
        .update(toUpdateID,personObject)
        .then(returnedPerson => { 
          setPersons(persons.map(person => 
            person.name !== personObject.name ? person : returnedPerson))
          setNotification(`${personObject.name} was successfully updated in the phonebook`)
          setTimeout(() => {
            setNotification(null)
          }, 2000)
        })
        .catch(error => {
          setNotification(`there was an error while updating, please refresh`)
          setTimeout(() => {
            setNotification(null)
          }, 2000)
        })
        setNewName('')
        setNewNumber('')
    } 
  }   else if (numberList.includes(personObject.number)) {
      window.alert(`a contact with the number ${personObject.number} is already in phonebook`)
      const confirm = window.confirm(`would you like to change the contact for ${personObject.number}?`)
      if(confirm) {
        const toUpdateID = persons.find(p => p.number === personObject.number).id
        personService
        .update(toUpdateID,personObject)
        .then(returnedPerson => { 
          setPersons(persons.map(person => person.number !== personObject.number ? person : returnedPerson))
          setNotification(`contact belonging to ${personObject.number} was successfully updated in the phonebook`)
          setTimeout(() => {
            setNotification(null)
          }, 2000)
        })
        .catch(error => {
          setNotification(`there was an error while updating, please refresh`)
          setTimeout(() => {
            setNotification(null)
          }, 2000)
        })
        setNewName('')
        setNewNumber('')
      }
    } else {
      personService
    .create(personObject)
    .then(returnedPerson => {
      setPersons(persons.concat(returnedPerson))
      setNotification(`${personObject.name} was successfully added to the phonebook`)
      setTimeout(() => {
        setNotification(null)
      }, 2000)
      setNewName('')
      setNewNumber('')
    })
    .catch(error => {
      setNotification(`there was an error while adding the contact, please refresh`)
      setTimeout(() => {
        setNotification(null)
      }, 2000)
    })
  }
}

  const filteredPersons = persons.filter(p => p.name.toLowerCase().includes(filter))

  return (
    <div>
      <Notification message={notification} />
      <div>debug name:{newName}</div>
      <div>debug number:{newNumber}</div>
      <div>debug filter:{filter}</div>
      <Header text="phonebook app" />
      <Filter filter={filter} handleFilter={handleFilter} />
      <Header text="add new" />
      <Form addPerson={addPerson} newName={newName} newNumber={newNumber} handleNameChange={handleNameChange} handleNumberChange={handleNumberChange}/>
      <Header text="contacts" />
      <ul>
        {filteredPersons.map(person => 
          <Person setPersons={setPersons} setNotification={setNotification} persons={persons} key={person.id} person={person}/>
        )}
      </ul>
      <Footer />
    </div>
  )
}

export default App